/** css de la page addList.ejs */
#list__add {
  position: relative;
  margin: 0 auto;
  right: 0;
  top: 90px;
  left: 0;
  width: 90%;
  height: auto;
  padding: 5px; }
  #list__add .list__form {
    margin: 20px 0; }
    #list__add .list__form .list__message {
      text-align: center;
      background-color: #5caa5c;
      border-radius: 3px;
      width: 60%;
      margin: 0 auto; }
    #list__add .list__form label {
      font-size: 20px;
      font-weight: bold; }
    #list__add .list__form input[type="text"] {
      width: 300px;
      height: 30px;
      border-radius: 3px;
      border: 1px solid grey; }
      #list__add .list__form input[type="text"]:focus {
        border: none; }
    #list__add .list__form span {
      color: red;
      font-weight: bold;
      font-size: 18px;
      display: none; }
  #list__add .list__products {
    position: relative;
    top: 0;
    left: 0;
    padding: 10px;
    width: 100%;
    z-index: 5;
    display: grid;
    grid-template-columns: repeat(6, calc(98%/6));
    gap: 5px; }
    #list__add .list__products .list__category {
      position: relative;
      text-align: inherit;
      border: 1px solid #494949;
      padding: 5px;
      float: left;
      height: auto;
      text-transform: uppercase;
      font-size: 16px;
      background-color: whitesmoke;
      border-radius: 5px;
      margin-left: 5px;
      margin-top: 5px;
      left: 0;
      top: 0; }
      #list__add .list__products .list__category .list__category-name {
        margin: 3px 0;
        text-decoration: underline; }
      #list__add .list__products .list__category ul,
      #list__add .list__products .list__category li {
        margin: 0;
        padding: 0;
        border: 0; }
      #list__add .list__products .list__category label {
        padding: 3px;
        cursor: pointer; }
        #list__add .list__products .list__category label input {
          float: right; }
      #list__add .list__products .list__category li {
        list-style-type: none; }
      #list__add .list__products .list__category .list__product-name {
        text-transform: capitalize; }
  #list__add .list__actions {
    text-align: center;
    position: relative;
    margin: 10px auto;
    left: 0;
    right: 0;
    padding: 10px; }
    #list__add .list__actions button {
      border: 1px solid grey;
      height: 36px;
      width: 200px;
      border-radius: 3px;
      padding: 5px;
      height: 35px;
      margin-left: 20px;
      cursor: pointer; }

#edit__list {
  margin: 100px auto 30px auto;
  border: 1px solid red;
  padding: 5px;
  width: 80%; }

* {
  box-sizing: border-box;
  font-family: roboto; }

body {
  padding: 0;
  margin: 0;
  border: 0; }

#header {
  box-sizing: border-box;
  background-color: #90A7FA;
  width: 100%;
  height: 80px;
  padding: 5px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9; }

/** CSS de la page index.ejs */
#shopping__lists {
  position: relative;
  top: 120px;
  margin: 10px auto;
  left: 0;
  width: 60%;
  padding: 5px;
  height: auto;
  margin-bottom: 10px;
  overflow-y: auto; }
  #shopping__lists .success {
    background-color: green;
    border-radius: 3px;
    color: white;
    text-align: center;
    font-size: 18px;
    padding: 2px; }
  #shopping__lists #recherche {
    width: 100%;
    height: 30px;
    margin: 10px 0;
    font: size 1.5rem;
    border-radius: 3px; }
  #shopping__lists table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    background-color: whitesmoke;
    width: 100%;
    border: 1px solid #e4e4e4;
    margin-bottom: 10px;
    margin: 0 auto;
    a-cursor: pointer; }
  #shopping__lists td,
  #shopping__lists th {
    border-top: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
    font-style: bold;
    font-size: 18px;
    word-wrap: break-word;
    width: 50%; }
    #shopping__lists td:nth-child(4),
    #shopping__lists th:nth-child(4) {
      text-align: center; }
  #shopping__lists td:nth-child(2) {
    text-align: center; }
    #shopping__lists td:nth-child(2) a {
      margin-left: 5%;
      cursor: pointer; }
  #shopping__lists tr:nth-child(even) {
    background-color: #dddddd; }

@charset "UTF-8";
/**
    Pour les médias avec largeur maximale de 1080px
*/
@media screen and (max-width: 1080px) {
  #list__ .list__products {
    position: relative;
    top: 0;
    left: 0;
    padding: 10px;
    width: 100%;
    z-index: 5;
    display: grid;
    grid-template-columns: repeat(4, calc(98%/4));
    gap: 5px; } }

/**
    Pour les médias avec une largeur maximale de 600px
*/
@media screen and (max-width: 800px) {
  .no-display {
    display: none; }
  #shopping__lists {
    width: 100%;
    text-align: center; }
    #shopping__lists td:nth-child(2) > a {
      margin-left: 10px; }
  /** Page d'ajout de liste */
  #list__add .list__form {
    margin: 0; }
    #list__add .list__form input[type="text"],
    #list__add .list__form input[type="checkbox"] {
      width: 100%;
      font-size: 20px; }
  #list__add .list__products {
    display: block; }
    #list__add .list__products > .list__category {
      display: block;
      width: 100%; }
      #list__add .list__products > .list__category label {
        text-align: center;
        font-size: 24px; }
  #list__add .list__actions button {
    margin-top: 10px;
    font-size: 20px; }
  /** Page de vue des listes */
  #view__list {
    width: 100%;
    margin: 0; }
    #view__list h2.view__title {
      margin-left: 0;
      text-align: left;
      padding: 0;
      color: blue; } }


#view__list {
  margin: 100px auto 20px auto;
  width: 60%; }
  #view__list .view__title {
    padding-left: 20px;
    text-transform: uppercase; }
  #view__list .view__products {
    text-align: justify; }
    #view__list .view__products li.view__product {
      width: 250px;
      list-style-type: circle;
      text-transform: capitalize;
      padding: 5px;
      cursor: pointer; }
      #view__list .view__products li.view__product span {
        color: red;
        float: right;
        font-size: 16px; }
    #view__list .view__products .done {
      text-decoration: line-through;
      color: grey; }
